.Ticker {
  background-color: #40a4c8;
  color: white;
  width: 100%;
  position: fixed;
  left: 0;
  bottom: 0;
}

.Marquee {
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  padding-left: 100%;
  animation: Marquee 3s linear infinite;
}

@keyframes Marquee {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(-100%, 0);
  }
}
